<template>
  <div>
    <page-loading v-if="loading"></page-loading>

    <div v-if="!loading" class="row justify-content-center">
      <div class="col col-lg-6 col-md-6 col-sm-12 col-12">
        <vs-divider position="left">Edit User Information</vs-divider>

        <vx-card>
          <div class="vx-row">
            <div class="vx-col w-full">
              <div class="flex justify-content-center sm:flex-row">
                <img src="@/assets/images/custom/avatar.png" class="mr-8 rounded h-24 w-24"/>

              </div>
            </div>
          </div>

          <div class="vx-row mt-8">
            <div class="vx-col md:w-1/2 w-full">

              <vs-input
                data-vv-validate-on="blur"
                name="name"
                icon="icon icon-user"
                icon-pack="feather"
                label-placeholder="name"
                v-model="firstName"
                class="w-full mt-8"/>


              <vs-input
                data-vv-validate-on="blur"
                name="email"
                icon="icon icon-mail"
                icon-pack="feather"
                label-placeholder="email"
                v-model="email"
                class="w-full mt-8"/>
              <span v-if="emailError" class="text-danger text-sm">{{ emailError }}</span>


            </div>
            <div class="vx-col md:w-1/2 w-full">

              <vs-input
                data-vv-validate-on="blur"
                name="family"
                icon="icon icon-user"
                icon-pack="feather"
                label-placeholder="lastname"
                v-model="lastName"
                class="w-full mt-8"/>


            </div>
            <div class="vx-col w-full">
              <v-select class="w-full mt-8" style="width: fit-content; min-width: 250px"
                        v-model="selectedEnglishLevel" :options="englishLevels" dir="ltr"/>
              <span v-if="levelError" class="text-danger text-sm">{{ levelError }}</span>
            </div>

          </div>

          <div class="vx-row">
            <div class="vx-col w-full">
              <div class="mt-8 flex flex-wrap items-center justify-end">
                <vs-button @click="saveUserDetailChanges()" id="save-changes-btn" class="ml-auto mt-2">save changes
                </vs-button>
              </div>
            </div>
          </div>
        </vx-card>

      </div>
    </div>

  </div>
</template>

<script>
import axios from '../../../http/axios'
import mixins from '../../../mixins'
import StatisticsCardLine from '@/components/statistics-cards/StatisticsCardLine.vue'
import vSelect from 'vue-select'

export default {
  mixins: [mixins],
  components: {
    StatisticsCardLine,
    'v-select': vSelect
  },
  name: 'Index',
  data () {
    return {
      loading: false,

      englishLevels: [{
        id: null,
        label: 'choose your english level'
      }],
      selectedEnglishLevel: {
        id: null,
        label: 'choose your english level'
      },
      levelError: null,

      userId: null,
      firstName: null,
      lastName: null,
      email: null,
      emailError: null,
      hasSubsections: false,
      remainingDays: 0,
      numberOfLifeTimePackages: 0,
      transactionHistoryData: []
    }
  },

  mounted () {
    this.getUserDetail()
    this.getEnglishLevels()
    this.getUserPackages()
  },

  methods: {
    getUserDetail () {

      let userId = localStorage.getItem('id')
      this.loading = true
      axios.get(`/api/users/${userId}/`, {
        headers: {
          Accept: 'application/json',
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200) {
          this.firstName = res.data.first_name === null ? '' : res.data.first_name
          this.lastName = res.data.last_name === null ? '' : res.data.last_name
          this.email = res.data.email === null ? '' : res.data.email
          this.selectedEnglishLevel = res.data.level
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger')
        }
      })
    },


    getUserPackages () {
      this.loading = true
      axios.get(`/api/store/user-packages/`, {
        headers: {
          Accept: 'application/json',
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        this.loading = false
        if (res.status === 200) {
          res.data.forEach(item => {
            if (item.package.type.id === this.SUBSCRIPTION_TYPE) {
              this.hasSubsections = true
              this.remainingDays = Math.ceil((Date.parse(item.expires_at) - Date.now()) / 1000 / 60 / (60 * 24))
            } else if (item.package.type.id === this.LIFETIME_TYPE) {
              this.numberOfLifeTimePackages += 1
            }
          })
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger')
        }
      })
    },

    saveUserDetailChanges () {

      let emailVal = this.validateEmail(this.email)
      let levelVal = this.validateField(this.selectedEnglishLevel.id, 'english level', 'select')

      this.emailError = emailVal.message
      this.levelError = levelVal.message

      if (!emailVal.validation || !levelVal.validation)
        return

      let userId = localStorage.getItem('id')
      this.startLoading('save-changes-btn')
      axios.put(`/api/users/${userId}/`, {
          first_name: this.firstName,
          last_name: this.lastName,
          email: this.email,
          level: this.selectedEnglishLevel.id
        },
        {
          headers: {
            Accept: 'application/json',
            Authorization: this.getAccessToken()
          }
        }).then(res => {
        this.stopLoading('save-changes-btn')
        if (res.status === 200) {
          this.firstName = res.data.first_name === null ? '' : res.data.first_name
          this.lastName = res.data.last_name === null ? '' : res.data.last_name
          this.email = res.data.email === null ? '' : res.data.email
          this.simpleNotify(this.NOTIFY_SUCCESS_TITLE, 'edited successfully', 'success')
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger')
        }
      })
    },

    getEnglishLevels () {
      axios.get(`/api/english/english-levels`, {
        headers: {
          Accept: 'application/json'
        }
      }).then(res => {
        if (res.status === 200) {
          res.data.forEach(s => {
            this.englishLevels.push({
              id: s.id,
              label: s.title
            })
          })
          this.selectedEnglishLevel = this.englishLevels.filter((item) => {
            return +item.id === +this.selectedEnglishLevel
          })
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger')
        }
      })
    }
  }

}
</script>

